<!--
 * @Description: 找货填写资料
 * @Version: Do not edit
 * @Autor: gitxinghui
 * @Date: 2020-12-02 17:33:11
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2021-01-05 01:05:50
-->
<template>
    <view class="things_freight_fill">
		<!-- tabbar -->
        <cu-custom><block slot="content">抢单</block></cu-custom>
        <form class="things_freight_fill_form" @submit="formSubmit">
            <!-- 地址 -->
			<view class="cu-form-group">
				<view class="title">装车地点：</view>
				<view class="flex justify-start align-center title title_not start_address" @tap="wPickerStart.visible=true"><view class="before_circle"></view>{{wPickerStart.value.length ? (wPickerStart.value):'选择装载地点'}}</view>
				<input type="text" class="hide" name="loading_region" :value="wPickerStart.value"/>
			</view>
			<view class="cu-form-group">
				<view class="title">卸车地点：</view>
				<view class="flex justify-start align-center title title_not end_address" @tap="wPickerEnd.visible=true"><view class="before_circle"></view>{{wPickerEnd.value.length ? (wPickerEnd.value):'选择卸货地点'}}</view>
				<input type="text" class="hide" name="unloading_region" :value="wPickerEnd.value"/>
			</view>
			<view class="cu-form-group">
				<view class="title">车牌号：</view>
				<input class="valueInput" placeholder="请填写" placeholder-class="placeHolderText" name="car_number" :value="carPicker.car_number" :disabled="true" @tap="openPlatePicker(1)"/>
                <input class="hide" name="car_number" :value="carPicker.car_number"/>
			</view>
            <view class="cu-form-group">
				<view class="title">车辆类型：</view>
				<input class="valueInput" placeholder="请填写" placeholder-class="placeHolderText" name="car_type" :disabled="true" :value="wPickerCar.label" @tap="wPickerCar.visible=true"/>
                <input class="hide" name="car_type" :value="wPickerCar.value"/>
			</view>
            <view class="cu-form-group">
				<view class="title">挂车牌号：</view>
				<input class="valueInput" placeholder="请填写" placeholder-class="placeHolderText" name="listing_car_number" :value="carPicker.listing_car_number" :disabled="true" @tap="openPlatePicker(2)"/>
                <input class="hide" name="listing_car_number" :value="carPicker.listing_car_number"/>
			</view>
            <view class="cu-form-group">
				<view class="title">车内灌质：</view>
				<input class="valueInput" placeholder="请填写" placeholder-class="placeHolderText" name="car_filling"/>
			</view>

            <view class="cu-form-group">
				<view class="title">司机电话：</view>
				<input class="valueInput" placeholder="请输入手机号码" name="mobile"/>
			</view>
            <view class="cu-form-group">
				<view class="title">司机姓名：</view>
				<input class="valueInput" placeholder="请输入司机姓名" placeholder-class="placeHolderText" name="realname"/>
			</view>
            <view class="cu-form-group">
				<view class="title">司机身份证号：</view>
				<input class="valueInput" type="idcard" placeholder="请输入司机身份证号" placeholder-class="placeHolderText" name="id_card"/>
			</view>

            <view class="cu-form-group">
				<view class="title">押运员姓名：</view>
				<input class="valueInput" placeholder="请输入押运员姓名" name="escort_name"/>
			</view>
            <view class="cu-form-group">
				<view class="title">押运员身份证号：</view>
				<input class="valueInput" type="idcard" placeholder="请输入押运员身份证号" name="escort_id_card" />
			</view>
            <view class="cu-form-group">
				<view class="title">灌体立方数：</view>
				<input class="valueInput" placeholder="请输入灌体立方数" type="digit" name="cubic_number"/>
			</view>
            <view class="cu-form-group">
				<view class="title">车辆所属公司：</view>
				<input class="valueInput" placeholder="请输入车辆所属公司" name="car_company"/>
			</view>
            <view class="cu-form-group remarks_input">
                <view class="title">备注：</view>
                <textarea class="inputText text_input" placeholder-class="placeHolderText" maxlength="-1" placeholder="请填写备注..." name="remark"></textarea>
            </view>
            <!-- 保存 -->
            <view class="flex justify-center align-center submit_btn_box">
                <button class="cu-btn submitBtn" form-type="submit" :disabled="buttonType">抢单</button>
            </view>
			 <!-- 装载-城市三级联动框 -->
			<w-picker 
				:visible.sync="wPickerStart.visible"
				mode="region"
				:value="wPickerStart.value"
				default-type="name"
				:level="3"
				:hide-area="false"
				@confirm="selectAddressStart($event,'region')"
				themeColor="#32d08b"
				ref="regionStart">
			</w-picker>
			<!-- 卸载-城市三级联动框 -->
			<w-picker 
				:visible.sync="wPickerEnd.visible"
				mode="region"
				:value="wPickerEnd.value"
				default-type="name"
				:level="3"
				:hide-area="false"
				@confirm="selectAddressEnd($event,'region')"
				themeColor="#32d08b"
				ref="regionEnd">
			</w-picker>
			<!-- 货物类型 -->
			<w-picker 
				:visible.sync="wPickerFreight.visible"
				mode="selector"
				:value="wPickerFreight.value"
				default-type="name"
				:default-props="wPickerFreight.defaultProps"
				:options="wPickerFreight.list"
				@confirm="selectFreight($event,'selector')"
				themeColor="#32d08b"
				ref="freightPicker">
			</w-picker>
			<!-- 货车类型 -->
			<w-picker 
				:visible.sync="wPickerCar.visible"
				mode="selector"
				:value="wPickerCar.value"
				default-type="name"
				:default-props="wPickerCar.defaultProps"
				:options="wPickerCar.list"
				@confirm="selectCar($event,'selector')"
				themeColor="#32d08b"
				ref="carPicker">
			</w-picker>
			<!-- 折扣类型 -->
			<w-picker 
				:visible.sync="wPickerDiscount.visible"
				mode="selector"
				:options="base_discountData"
				@confirm="selectDiscount($event,'selector')"
				themeColor="#32d08b"
				ref="discountPicker">
			</w-picker>
			<!-- 车牌号输入 -->
			<plateInput v-if="carPicker.plateShow" :plate="carPicker.plateNo" @export="setPlate" @close="carPicker.plateShow = false"/>
        </form>
		<!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
    import { grabbingGoodsOrder_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
	let that = null;
export default {
    data() {
        return {
			order_id: '',
            // 全局按钮状态
            buttonType: false,
            appBaseData:null,//全局配置信息
            userInfo: {},
            wPickerStart: {
                visible: false,
                result: {
                    value: ''
                },
                value: [],
                defaultRegion:["广东省","茂名市","茂南区"]
            },
            wPickerEnd: {
                visible: false,
                value: [],
                defaultRegion:["广东省","茂名市","茂南区"]
			},
			// 货物picker
            wPickerFreight: {
                visible: false,
                value: '',
                label: '',
                list: storage.get("appBaseData").wxapp.goods_type,
                defaultProps: {"label":"name","value":"value"}
            },
            // 货车类型
            wPickerCar: {
                visible: false,
                value: '',
                list: storage.get("appBaseData").wxapp.car_type,
                defaultProps: {"label":"name","value":"value"}
            },
            // 货车型号输入
            carPicker: {
                plateShow: false,
                plateNo: '',
                car_number:'',
                listing_car_number: '',
                type: 1,// 1:车牌号，2：挂车牌号
            },
            // 折扣类型
            wPickerDiscount: {
                visible: false,
                value: '',
                label: '',
                list: [],
            },
            // 开始时间
            start_time: '',
            // 结束时间
            end_time: '',
        }
	},
	watch: {
        '$store.getters.base_appBaseData': function(val) {
            that.appBaseData = val;
            that.wPickerCar.list = val.wxapp.car_type;
        },
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    computed: {
        ...mapGetters(['base_discountData']) 
    },
    methods: {
		...mapMutations([
            'modal',
            'user',
            'base'
		]),
		/**开始时间选择
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        startDateChange(e) {
            that.start_time = e.detail.value;
        },
        /**结束时间选择
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        endDateChange(e) {
            that.end_time = e.detail.value;
		},
		/**打开货物弹框
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        openWPickerFreight(e) {
            that.wPickerFreight.visible = true;
        },
        /**选择货物
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        selectFreight(e) {
            that.wPickerFreight.label = e.result;
            that.wPickerFreight.value = e.value;
        },
        /**装货地点选择
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        selectAddressStart(e) {
            that.wPickerStart.value = e.result;
        },
        /**卸货地点选择
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        selectAddressEnd(e) {
            that.wPickerEnd.value = e.result;
        },
        /**打开车牌输入框
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        openPlatePicker(e) {
            that.carPicker.type = e;
            if (e==1) {
                that.carPicker.plateNo = that.carPicker.car_number;
            }
            if (e==2) {
                that.carPicker.plateNo = that.carPicker.listing_car_number;
            }
            that.carPicker.plateShow = true;
        },
        /**设置车牌号
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        setPlate(e) {
            if (e.length >= 7) {
                if (that.carPicker.type === 1) that.carPicker.car_number = e;
                if (that.carPicker.type === 2) that.carPicker.listing_car_number = e;
            };
			that.carPicker.plateShow = false;
        },
        /**选择车
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        selectCar(e) {
            that.wPickerCar.label = e.result;
            that.wPickerCar.value = e.value;
        },
        /**选择折扣
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        selectDiscount(e) {
            that.wPickerDiscount.label = e.result;
            that.wPickerDiscount.value = e.value;
        },
        /**
         * @description: 提交表单
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        formSubmit(e) {
             //定义表单规则
            let rule = [
                {name:"loading_region", checkType : "notnull", checkRule:"",  errorMsg:"请选择装载地址"},
                {name:"unloading_region", checkType : "notnull", checkRule:"",  errorMsg:"请选择卸载地址"},
                {name:"car_number", checkType : "notnull", checkRule:"",  errorMsg:"请输入车牌号"},
                {name:"listing_car_number", checkType : "morethanzero",  errorMsg:"请输入挂牌号"},
                {name:"car_filling", checkType : "notnull", checkRule:"",  errorMsg:"请输入车内罐质"},
                {name:"mobile", checkType : "morethanzero",  errorMsg:"请输入司机手机号码"},
                {name:"realname", checkType : "notnull", checkRule:"",  errorMsg:"请输入司机真实姓名"},
                {name:"id_card", checkType : "notnull", checkRule:"",  errorMsg:"请输入司机身份证号码"},
                {name:"escort_name", checkType : "morethanzero",  errorMsg:"请输入押运员姓名"},
                {name:"escort_id_card", checkType : "notnull",checkRule:"",  errorMsg:"请输入押运员身份证号码"},
                {name:"car_type", checkType : "notnull", checkRule:"",  errorMsg:"请选择车辆种类"},
                {name:"cubic_number", checkType : "notnull", checkRule:"",  errorMsg:"请输入罐体立方数(m3)"},
				{name:"car_company", checkType : "notnull", checkRule:"",  errorMsg:"请输入车辆所属公司"},
            ];
            //进行表单检查
            let formData = e.detail.value;
            let checkRes = checkForm.check(formData, rule);
            if(!checkRes){
                that.modal({name:'popTipModal',value:{type: true,tipTitle:'表单错误',tipContent:checkForm.error}});
                return false;
			}
			formData.order_id = that.order_id;
            that.modal({name:'popLoading',value:true});
            that.base({name:'buttonType',value:true});
            grabbingGoodsOrder_api(formData,that).then(res=>{
                that.modal({name:'popLoading',value:false});
                that.base({name:'buttonType',value:false});
                that.base({name:'refreshPage',value:true});
                that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'抢单成功',linkUrl:1}});
            }).catch(err=>{
                console.log(err);
                that.modal({name:'popLoading',value:false});
                that.base({name:'buttonType',value:false});
            })
        }
	},
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        if (!options.order_id || options.order_id == ''|| options.order_id == 'undefined') {
            that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'该订单不存在',linkUrl:1}});
            return false;
        }
        that.order_id = options.order_id;
    }
}
</script>
<style lang="scss" scoped>
    @import './index';
</style>